import * as React from 'react';
import './App.css';
import Button from '@mui/material/Button';
import { createTheme } from '@mui/material/styles';
import { ThemeProvider } from '@emotion/react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedinIcon from '@mui/icons-material/LinkedIn';
import TwitterIcon from '@mui/icons-material/Twitter';
import Paper from '@mui/material/Paper';
import Snackbar from '@mui/material/Snackbar';
import TextField from '@mui/material/TextField';
import MuiAlert, { AlertProps } from '@mui/material/Alert';
import LoadingButton from '@mui/lab/LoadingButton';

const Alert = React.forwardRef(function Alert(
  props,
  ref,
) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const theme = createTheme({
  status: {
    danger: '#f4bf37',
  },
  palette: {
    primary: {
      main: '#f4bf37',
      darker: '#f4bf37',
    },
    neutral: {
      main: '#64748B',
      contrastText: '#fff',
    },
  },
});

function App() {

  const [open, setOpen] = React.useState(false);
  const [loading, setLoading] = React.useState(false);

  const handleClick = () => {
    setLoading(true);
    setTimeout(() => {
      setOpen(true);
      setLoading(false);
    }, (Math.random() * 3 + 1) * 4000);
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };

  const homeRef = React.useRef(null);
  const aboutRef = React.useRef(null);
  const contactRef = React.useRef(null);

  return (
    <ThemeProvider theme={theme}>
      <Box sx={{ flexGrow: 1 }}>
        <AppBar position="fixed">
          <Toolbar>
            <Button size="large" color="inherit" onClick={() => homeRef.current?.scrollIntoView({behavior: 'smooth'})}>Home</Button>
            <Button size="large" color="inherit" onClick={() => aboutRef.current?.scrollIntoView({behavior: 'smooth'})}>About</Button>
            <Button size="large" color="inherit" onClick={() => contactRef.current?.scrollIntoView({behavior: 'smooth'})}>Contact</Button>
          </Toolbar>
        </AppBar>
      </Box>
      <div ref={homeRef} className="main-container">
        <div className="intro-container">
          <h1 className="intro-text">Jim Ho</h1>
          <h2 className="intro-text intro-quote">Senior Consultant</h2>
        </div>
      </div>
      <div className="about-container">
        <div className="about-header-container">
          <h1 ref={aboutRef} className="about-header-text">About</h1>
          <div className="about-content-container">
            <div className="about-details-container">
              <h2 className="about-quote"><i>Take the first step, and the rest will follow.</i> <br></br> Jim Ho</h2>
              <p className="about-text">
                Jim is a tech consultant with many years of experience in the industry.
                He has provided his expertise to notable corporations such as Oracle in the past and he is currently employed as a senior consultant at KPMG.
                In his free time, besides keeping himself up to date with the latest breakthroughs in technology, Jim can be found playing badminton at the local rec centre or competing at the highest level in the popular autochess game, Teamfight Tactics. 
              </p>
              <Button
                variant="contained"
                size="large"
                sx={{ marginTop: '16px' }}
                onClick={() => contactRef.current?.scrollIntoView({behavior: 'smooth'})}
              >
                Contact
              </Button>
            </div>
            <div className="about-portrait-container">
              <img className="about-portrait" src={require("./portrait.jpeg")}></img>
            </div>
          </div>
        </div>
      </div>
      <div className="contact-header-container">
        <h1 ref={contactRef}>Contact</h1>
      </div>
      <div className="contact-gradient">
      </div>
      <div className="contact-container">
        <Paper
          elevation={10}
          sx={{
            width: 640,
            height: 500,
            display: 'flex',
            justifyContent: 'center',
            flexDdirection: 'column'
          }}
        >
          <div className="contact-text-container">
            <TextField
              id="outlined-basic"
              label="Enter the subject"
              variant="filled"
              sx={{
                width: '560px',
                margin: '20px',
              }}
            />
            <TextField
              id="outlined-basic"
              label="Enter your name"
              variant="filled"
              sx={{
                width: '560px',
                margin: '20px',
              }}
            />
            <TextField
              id="outlined-basic"
              label="Enter the message"
              variant="filled"
              multiline
              rows={7}
              sx={{
                width: '560px',
                margin: '20px',
              }}
            />
            <Snackbar
              open={open}
              autoHideDuration={6000}
              onClose={handleClose}
              anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
            <Alert onClose={handleClose} severity="error">
              Failed to send message
            </Alert>
      </Snackbar>
            <LoadingButton
              size="large"
              loading={loading}
              onClick={handleClick}
            >
              Submit
            </LoadingButton>
          </div>
        </Paper>
      </div>
      <div className="bottom-bar-container">
        <h1 className="bottom-bar-header">Social Media</h1>
        <div className="bottom-bar-socials-container">
          <div className="bottom-bar-social">
            <IconButton aria-label="share" href="https://github.com/eugenelu12">
              <GitHubIcon fontSize="large" htmlColor="#f9df9b" />
            </IconButton>
          </div>
          <div className="bottom-bar-social">
            <IconButton aria-label="share" href="https://linkedin.com/in/eugenelu1">
              <LinkedinIcon fontSize="large" htmlColor="#f9df9b" />
            </IconButton>
          </div>
          <div className="bottom-bar-social">
            <IconButton aria-label="share" href="">
              <TwitterIcon fontSize="large" htmlColor="#f9df9b" />
            </IconButton>
          </div>
        </div>
        <p className="bottom-bar-text">Jim Ho, Made by Eugene Lu  2022</p>
      </div>
    </ThemeProvider>
  );
}

export default App;
